
.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 200px;

  // margin: 0 24px 50px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background-color: white;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  font-style: normal;
  padding: 15px 14px;
  &__photo {
    display: block;
    width: 160px;
    height: 134px;
    margin: 15px auto 45px;
    &:hover {
      border: 3px solid #f3f3f3;
      border-radius: 5px;
      margin: 12px 2px 42px;
      animation: img-grow 0.5s both linear;

    }
  }

  @keyframes img-grow {
    100% {
      width: 194px;
      height: 165px;
      margin: -16px -15px 39px ;

    }

  }

  &__productName {
    display: block;
    font-size: 12px;
    line-height: 15px;
    margin: 0 auto;
  }

  &__productCode {
    font-size: 11px;
    line-height: 13px;
    margin: 5px 0 0;
    font-weight: normal;

  }

  &__stars {
    margin-top: 15px;
  }

  &__priceContainer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 20px 0 0;
  }

  &__buy {
    width: 170px;
    height: 40px;
    background-color: #00acdc;
    text-align: center;
    line-height: 38px;
    text-decoration: none;
    font-family: Roboto, sans-serif;
    font-weight: bold;
    font-size: 14px;
    color: white;
    border-radius: 5px;
    margin: 20px auto 0;
    border: 1px solid #00acdc;
    box-sizing: border-box;
    transition: 0.3s;
    &:hover {
      background-color: white;
      color: #00acdc;
    }
  }

}
